
import Layout from "@/components/layout/Layout"
import Link from "next/link"
export default function Home() {

    return (
        <>
            <Layout headerStyle={3} footerStyle={1} breadcrumbTitle="Privacy Policy">
                <section className="blog-page-carousel">
                    <div className="container">
                        <div className="row">
                            <div className="col-xl-12">
                                <p>
                                    Thank you for visiting https://www.labondiamonds.com. Labon LLC respects your privacy and we understand and share your concern about the privacy of your personal information. This notice describes our privacy policies. By visiting https://www.labondiamonds.com, you are confirming your agreement to the privacy policies described in this Privacy Policy.
                                </p>
                                <h3 className="mt-3 mb-2">Information We Collect</h3>
                                <p>We collect and store any information that you may enter on any of our web sites or provide to us in some other way. For example, when you place an order and/or sign-up for email communications and updates, we collect and store personal information so that we may provide the products and services that you have ordered or requested. Such information may include your name, address, age, date of birth, gender, email address, telephone and fax numbers, credit card information and information about your interests and activities. You can choose not to provide certain types of information, but that may result in you being unable to use certain features of our web site(s). We may also collect and store information about other people that you provide to us. For example, you may submit a person`s name and address if you wish us to ship a product as a gift to a friend.</p>
                                <p>We may also automatically collect and store certain types of non-personally identifiable information whenever you visit any of our web pages. For example, like most commercial shopping web sites, we use "cookies" and we obtain certain types of information whenever your web browser accesses our web site. Such information may include the type of web browser you are using, the operating system of your computer and the domain name of your internet service provider.</p>
                                <h3 className="mt-3 mb-2">Use of Information</h3>
                                <p>All personal information that we collect from our customers is used to improve our relevance to you and to improve your shopping experience with us. We use the information that you provide so that we may provide the products and services that you have ordered or requested. In addition, we may send you (via email or other means) offerings of products or services which we think may be of interest to you or we may ask you to participate in activities such as special surveys. At any time, you can choose to no longer receive any non-transactional emails from Labon LLC by calling or emailing us to remove you from our system.</p>
                                <p>We will not share the personal information which you provide to us except (i) for the purposes that you provided it to us, (ii) with your prior consent, (iii) as may be required by law as we reasonably deem necessary to protect Labon LLC or others from injury or loss, or (iv) with other persons or entities with whom we contract to provide services in connection with our web site(s) or other business activities.</p>
                                <p>The personal information which you provide to Labon LLC is an asset of the company and may be included in the assets transferred to an acquirer if Labon LLC is acquired by a third party. In the event of such a transfer, any personal information transferred shall remain subject to the terms and promises contained in this Privacy Policy.</p>
                                <h3 className="mt-3 mb-2">Security</h3>
                                <p>We believe that Labon LLC, Inc. has in place appropriate procedures to protect the security of personal information transmitted to us or by us. We have designed our web site(s) to encrypt such information during transmission using Secure Sockets Layer software.</p>
                                <h3 className="mt-3 mb-2">Access to and Correction</h3>
                                <p>Labon LLC gives you access to the personal information that we have collected about you and the ability to correct any errors in such information.</p>
                                <h3 className="mt-3 mb-2">Terms of Use, Notice and Revisions</h3>
                                <p>If you visit any part of web site https://www.labondiamonds.com, your visit and any dispute concerning privacy will be subject to this Privacy Notice and our Terms of Use. Our business continues to develop and evolve and this Privacy Notice and our Terms of Use will accordingly change from time to time. We may send you reminders of our privacy policies and terms from time to time, by email or otherwise, unless you have told us not to do so. However, you should check our web site(s) from time to time to inform yourself of any changes. The form of our Privacy Policy in effect from time to time will apply to all information that we have collected.</p>
                            </div>
                        </div>
                    </div>
                </section>

            </Layout>
        </>
    )
}